@import "../../variables.scss";

.location-form-section {
  position: relative;
  margin-bottom: 0;
  background-position: center;
  background-size: cover;
  padding: 45px 0;
  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(23, 27, 48, 0.687);

  }
  &__inner {
    display: flex;
  }
  .title {
    text-transform: uppercase;
    margin-bottom: 32px;
    color: white;
  }
  .intro-text {
    margin-bottom: 23px;
    font-size: 20px;;
    text-transform: uppercase;
    line-height: 36px;
    color: white;
  }
  .form {
    display: flex;
    flex-direction: column;
    flex-grow: 1;

    &-container {
      z-index: 10;
      display: flex;
      flex-direction: column;
      padding-right: 60px;
    }
    &-field {
      width: 100%;
      padding: 10px;
      border: none;
      margin-bottom: 20px;
      &::placeholder {
        color: grey;
      }
    }
  }
  .map-img-container {
    display: flex;
    z-index: 10;
  }
}

@media only screen and (max-width:$desktop-breakpoint) {
  .location-form-section {
    padding: 30px 0;
    &__inner {
      flex-direction: column;
    }
    .form-container {
      padding-right: 0;
      .title {
        font-size: 22px;
        margin-bottom: 20px;
      }
      .intro-text {
        font-size: 16px;
      }
    }

    .name-email-wrapper {
      flex-direction: column;
    }

    .form-field {
      width: 100%;
    }

    .map-img-container {
      justify-content: center;
    }

    .map-img {
      display: none;
      // width: 100%;
      // max-width: 500px;
      // margin-top: 20px;
    }
  }
}